import { useEffect, useState, useMemo } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Navbar } from "@/components/Navbar";
import { Footer } from "@/components/Footer";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent } from "@/components/ui/dialog";
import { Calendar as CalendarIcon, Clock, User, Check, ArrowLeft, Crown } from "lucide-react";
import { DEFAULT_SERVICES, ServiceItem } from "@/data/services";
import { loadServices } from "@/lib/services-storage";
import { loadVipData, VipMember } from "@/lib/vips-storage";
import { PaymentScreen } from "@/components/pix/PaymentScreen";
import { SuccessScreen } from "@/components/pix/SuccessScreen";
import { generatePixCode } from "@/services/pixService";
import { PixPaymentState, GeneratedPix } from "@/types/pix";
import { loadBarbershops } from "@/lib/barbershops-storage";
import { useToast } from "@/components/ui/use-toast";

interface Appointment {
  id: string;
  serviceId: string;
  barberId: string;
  date: Date;
  time: string;
  clientName?: string;
}

interface PaymentData {
  fullName: string;
  phone: string;
  cpf: string;
}

const barbers = [
  {
    id: "miguel-santos",
    name: "Miguel Santos",
    specialty: "Cortes Clssicos",
  },
  {
    id: "rafael-costa",
    name: "Rafael Costa",
    specialty: "Barbas Desenhadas",
  },
  {
    id: "andre-silva",
    name: "Andr Silva",
    specialty: "Cortes Modernos",
  },
  {
    id: "lucas-oliveira",
    name: "Lucas Oliveira",
    specialty: "Colorao",
  },
];

const ConfirmBooking = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { toast } = useToast();
  const [services, setServices] = useState<ServiceItem[]>(DEFAULT_SERVICES);
  const [appointments, setAppointments] = useState<Appointment[]>([]);
  const [paymentData, setPaymentData] = useState<PaymentData>({
    fullName: "",
    phone: "",
    cpf: "",
  });
  const [vipMembers, setVipMembers] = useState<VipMember[]>([]);
  const [pixState, setPixState] = useState<PixPaymentState>(PixPaymentState.FORM);
  const [currentPix, setCurrentPix] = useState<GeneratedPix | null>(null);
  const [pixDialogOpen, setPixDialogOpen] = useState(false);
  const [selectedBarbershop, setSelectedBarbershop] = useState<{ id: string; name: string; pixKey: string } | null>(null);

  useEffect(() => {
    const nextServices = loadServices();
    setServices(nextServices);

    const vipData = loadVipData();
    setVipMembers(vipData.members);

    const storedAppointments = localStorage.getItem("bookingAppointments");
    if (storedAppointments) {
      try {
        const parsed = JSON.parse(storedAppointments);
        const appointmentsWithDates = parsed.map((apt: Omit<Appointment, "date"> & { date: string }) => ({
          ...apt,
          date: new Date(apt.date),
        }));
        setAppointments(appointmentsWithDates);
      } catch {
        navigate("/booking");
      }
    } else {
      navigate("/booking");
    }

    const storedBarbershop = localStorage.getItem("selectedBarbershop");
    if (storedBarbershop) {
      try {
        const parsed = JSON.parse(storedBarbershop) as { id: string | number; name: string; email?: string };
        const barbershops = loadBarbershops();
        const barbershop = barbershops.find(bs => 
          bs.id === parsed.id.toString() || bs.id === parsed.id
        );
        
        if (barbershop && barbershop.pixKey) {
          setSelectedBarbershop({
            id: barbershop.id,
            name: barbershop.name,
            pixKey: barbershop.pixKey,
          });
        }
      } catch {
        // Ignore parsing errors
      }
    }
  }, [navigate]);

  const handleClientNameChange = (appointmentId: string, name: string) => {
    setAppointments((prev) =>
      prev.map((apt) => (apt.id === appointmentId ? { ...apt, clientName: name } : apt))
    );
  };

  const handlePaymentDataChange = (field: keyof PaymentData, value: string) => {
    setPaymentData((prev) => ({ ...prev, [field]: value }));
  };

  const isVipClient = useMemo(() => {
    if (!paymentData.fullName || !paymentData.cpf || !paymentData.phone) {
      return null;
    }

    const normalizedName = paymentData.fullName.toLowerCase().trim();
    const normalizedCpf = paymentData.cpf.replace(/\D/g, "");
    const normalizedPhone = paymentData.phone.replace(/\D/g, "");

    const now = new Date();
    now.setHours(0, 0, 0, 0);

    return vipMembers.find((member) => {
      const memberName = member.name.toLowerCase().trim();
      const memberCpf = member.cpf.replace(/\D/g, "");
      const memberPhone = member.phone.replace(/\D/g, "");

      const matches = 
        memberName === normalizedName &&
        memberCpf === normalizedCpf &&
        memberPhone === normalizedPhone;

      if (!matches) return false;

      if (member.paymentStatus !== "paid") return false;

      const expiresAt = new Date(member.expiresAt);
      expiresAt.setHours(0, 0, 0, 0);

      return expiresAt >= now;
    }) || null;
  }, [paymentData.fullName, paymentData.cpf, paymentData.phone, vipMembers]);

  const formatCPF = (value: string) => {
    const numbers = value.replace(/\D/g, "");
    if (numbers.length <= 11) {
      return numbers.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, "$1.$2.$3-$4");
    }
    return value;
  };

  const formatPhone = (value: string) => {
    const numbers = value.replace(/\D/g, "");
    if (numbers.length <= 11) {
      return numbers.replace(/(\d{2})(\d{5})(\d{4})/, "($1) $2-$3");
    }
    return value;
  };

  const isPaymentFormValid = () => {
    return (
      paymentData.fullName.trim().length >= 3 &&
      paymentData.phone.replace(/\D/g, "").length >= 10 &&
      paymentData.cpf.replace(/\D/g, "").length === 11
    );
  };

  const areAllClientNamesFilled = () => {
    return appointments.every((apt) => apt.clientName && apt.clientName.trim().length > 0);
  };

  const handlePayment = () => {
    if (!isPaymentFormValid() || !areAllClientNamesFilled()) {
      return;
    }

    if (!selectedBarbershop || !selectedBarbershop.pixKey) {
      toast({
        variant: "destructive",
        title: "Chave PIX no configurada",
        description: "A barbearia selecionada no possui chave PIX configurada. Configure no painel administrativo.",
      });
      return;
    }

    const total = calculateTotal();
    const amount = total.toFixed(2).replace('.', ',');
    const pixData = {
      key: selectedBarbershop.pixKey,
      amount: amount,
      name: selectedBarbershop.name,
      city: "So Paulo",
      txId: `AGENDAMENTO-${Date.now()}`,
    };

    const payload = generatePixCode(pixData);
    
    setCurrentPix({
      payload,
      data: pixData,
    });
    
    setPixState(PixPaymentState.PAYMENT);
    setPixDialogOpen(true);
  };

  const handlePaymentConfirm = () => {
    const storedBarbershop = localStorage.getItem("selectedBarbershop");
    let barbershopId: string | null = null;
    
    if (storedBarbershop) {
      try {
        const parsed = JSON.parse(storedBarbershop) as { id?: string | number };
        if (parsed.id) {
          barbershopId = typeof parsed.id === "number" ? parsed.id.toString() : parsed.id;
        }
      } catch {
        // Ignore parsing errors
      }
    }
    
    const bookingData = {
      appointments: appointments.map((apt) => ({
        ...apt,
        date: apt.date.toISOString(),
        clientName: apt.clientName || paymentData.fullName || undefined,
      })),
      payment: paymentData,
      timestamp: new Date().toISOString(),
      barbershopId: barbershopId || undefined,
      isVip: isVipClient !== null,
      vipMemberId: isVipClient?.id,
    };
    
    const bookingKey = `bookingConfirmation_${Date.now()}_${Math.random().toString(36).substring(2, 15)}`;
    localStorage.setItem(bookingKey, JSON.stringify(bookingData));
    
    setPixState(PixPaymentState.SUCCESS);
    localStorage.removeItem("bookingAppointments");
  };

  const handlePixReset = () => {
    setPixState(PixPaymentState.FORM);
    setCurrentPix(null);
    setPixDialogOpen(false);
    navigate("/");
  };

  const handleBackToForm = () => {
    setPixState(PixPaymentState.PAYMENT);
  };

  const currencyFormatter = new Intl.NumberFormat("pt-BR", { style: "currency", currency: "BRL" });

  const calculateTotal = () => {
    let total = 0;
    const vipServiceFirstOccurrence = new Map<string, boolean>();
    const isClientVip = isVipClient !== null;
    
    appointments.forEach((apt) => {
      const service = services.find((s) => s.id === apt.serviceId);
      if (service) {
        const hasDiscount =
          service.promotionScope !== "none" &&
          service.discountPercentage !== null &&
          service.discountPercentage > 0;
        
        if (hasDiscount && service.promotionScope === "vip") {
          if (isClientVip) {
            const isFirst = !vipServiceFirstOccurrence.has(apt.serviceId);
            vipServiceFirstOccurrence.set(apt.serviceId, true);
            
            if (isFirst) {
              const discountedPrice = service.price * (1 - (service.discountPercentage! / 100));
              total += discountedPrice;
            } else {
              total += service.price;
            }
          } else {
            total += service.price;
          }
        } else if (hasDiscount) {
          const discountedPrice = service.price * (1 - (service.discountPercentage! / 100));
          total += discountedPrice;
        } else {
          total += service.price;
        }
      }
    });
    return total;
  };

  return (
    <div className="min-h-screen">
      <Navbar />
      
      <main className="pt-32 pb-20">
        <div className="container mx-auto px-4 max-w-4xl">
          <Button
            variant="ghost"
            onClick={() => navigate("/booking")}
            className="mb-6"
          >
            <ArrowLeft className="h-4 w-4 mr-2" />
            Voltar
          </Button>

          <div className="text-center mb-12">
            <h1 className="text-4xl md:text-5xl font-display font-bold mb-4">
              Confirmar <span className="text-primary">Agendamento</span>
            </h1>
            <p className="text-xl text-muted-foreground">
              Revise seus agendamentos e complete os dados para finalizar
            </p>
          </div>

          <div className="space-y-6">
            {/* Appointments Section */}
            <Card className="shadow-card border-border">
              <CardHeader>
                <CardTitle className="text-2xl">Agendamentos</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {appointments.map((appointment, index) => {
                  const service = services.find((s) => s.id === appointment.serviceId);
                  const barber = barbers.find((b) => b.id === appointment.barberId);
                  const dateStr = appointment.date.toLocaleDateString("pt-BR");
                  
                  const hasDiscount =
                    service?.promotionScope !== "none" &&
                    service?.discountPercentage !== null &&
                    service?.discountPercentage > 0;
                  
                  const isClientVip = isVipClient !== null;
                  const isVipService = service?.promotionScope === "vip";
                  const isVipFirstOccurrence = hasDiscount && 
                    isVipService &&
                    isClientVip &&
                    appointments.findIndex((apt) => apt.serviceId === appointment.serviceId) === index;
                  
                  const finalPrice = hasDiscount && isVipService
                    ? (isClientVip && isVipFirstOccurrence 
                        ? service!.price * (1 - (service!.discountPercentage! / 100))
                        : service!.price)
                    : hasDiscount
                      ? service!.price * (1 - (service!.discountPercentage! / 100))
                      : service?.price || 0;

                  return (
                    <div key={appointment.id} className="p-4 border border-border rounded-lg space-y-3">
                      <div className="flex items-start justify-between">
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-2">
                            <h3 className="font-semibold text-lg">{service?.title}</h3>
                            {isVipFirstOccurrence && (
                              <span className="text-xs bg-primary/10 text-primary px-2 py-1 rounded flex items-center gap-1">
                                <Crown className="h-3 w-3" />
                                VIP -{service?.discountPercentage}% (1 vez)
                              </span>
                            )}
                            {isVipService && hasDiscount && !isClientVip && (
                              <span className="text-xs bg-secondary text-muted-foreground px-2 py-1 rounded">
                                Desconto VIP no aplicvel
                              </span>
                            )}
                            {isVipService && isClientVip && !isVipFirstOccurrence && (
                              <span className="text-xs bg-secondary text-muted-foreground px-2 py-1 rounded">
                                Sem desconto VIP
                              </span>
                            )}
                            {service?.promotionScope !== "vip" && hasDiscount && (
                              <span className="text-xs bg-primary/10 text-primary px-2 py-1 rounded">
                                -{service?.discountPercentage}%
                              </span>
                            )}
                          </div>
                          <div className="space-y-1 text-sm text-muted-foreground">
                            <div className="flex items-center gap-2">
                              <User className="h-4 w-4" />
                              <span>{barber?.name}</span>
                            </div>
                            <div className="flex items-center gap-2">
                              <CalendarIcon className="h-4 w-4" />
                              <span>{dateStr}</span>
                            </div>
                            <div className="flex items-center gap-2">
                              <Clock className="h-4 w-4" />
                              <span>{appointment.time}</span>
                            </div>
                          </div>
                        </div>
                        <div className="text-right">
                          <p className="text-lg font-semibold text-primary">
                            {currencyFormatter.format(finalPrice)}
                          </p>
                          {hasDiscount && (
                            <p className="text-xs text-muted-foreground line-through">
                              {currencyFormatter.format(service!.price)}
                            </p>
                          )}
                        </div>
                      </div>
                      
                      <div>
                        <Label htmlFor={`name-${appointment.id}`} className="text-sm font-medium">
                          Nome do Cliente para este servio
                        </Label>
                        <Input
                          id={`name-${appointment.id}`}
                          value={appointment.clientName || ""}
                          onChange={(e) => handleClientNameChange(appointment.id, e.target.value)}
                          placeholder="Digite o nome do cliente"
                          className="mt-1"
                        />
                      </div>
                    </div>
                  );
                })}
              </CardContent>
            </Card>

            {/* Payment Section */}
            {areAllClientNamesFilled() && (
              <Card className="shadow-card border-border">
                <CardHeader>
                  <CardTitle className="text-2xl flex items-center gap-2">
                    Dados para Pagamento
                    {isVipClient && (
                      <span className="text-xs bg-primary/10 text-primary px-3 py-1 rounded-full flex items-center gap-1">
                        <Crown className="h-4 w-4" />
                        Cliente VIP
                      </span>
                    )}
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  {isVipClient && (
                    <div className="p-3 bg-primary/5 border border-primary/20 rounded-lg">
                      <p className="text-sm text-primary font-medium flex items-center gap-2">
                        <Crown className="h-4 w-4" />
                        Cliente VIP identificado! Descontos sero aplicados automaticamente.
                      </p>
                    </div>
                  )}
                  <div>
                    <Label htmlFor="fullName" className="text-sm font-medium">
                      Nome Completo <span className="text-destructive">*</span>
                    </Label>
                    <Input
                      id="fullName"
                      value={paymentData.fullName}
                      onChange={(e) => handlePaymentDataChange("fullName", e.target.value)}
                      placeholder="Digite seu nome completo"
                      className="mt-1"
                    />
                  </div>

                  <div>
                    <Label htmlFor="phone" className="text-sm font-medium">
                      Nmero de Telefone <span className="text-destructive">*</span>
                    </Label>
                    <Input
                      id="phone"
                      value={paymentData.phone}
                      onChange={(e) => handlePaymentDataChange("phone", formatPhone(e.target.value))}
                      placeholder="(00) 00000-0000"
                      className="mt-1"
                      maxLength={15}
                    />
                  </div>

                  <div>
                    <Label htmlFor="cpf" className="text-sm font-medium">
                      CPF <span className="text-destructive">*</span>
                    </Label>
                    <Input
                      id="cpf"
                      value={paymentData.cpf}
                      onChange={(e) => handlePaymentDataChange("cpf", formatCPF(e.target.value))}
                      placeholder="000.000.000-00"
                      className="mt-1"
                      maxLength={14}
                    />
                  </div>
                </CardContent>
              </Card>
            )}

            {/* Total and Payment Button */}
            <Card className="shadow-card border-border">
              <CardContent className="pt-6">
                {isVipClient === null && isPaymentFormValid() && (
                  <div className="mb-4 p-3 bg-card border border-border rounded-lg">
                    <p className="text-sm text-muted-foreground mb-1">Receita Normal</p>
                    <p className="text-lg font-semibold">{currencyFormatter.format(calculateTotal())}</p>
                  </div>
                )}
                <div className="flex items-center justify-between mb-6">
                  <span className="text-lg font-semibold">Total:</span>
                  <span className="text-2xl font-bold text-primary">
                    {currencyFormatter.format(calculateTotal())}
                  </span>
                </div>
                
                <Button
                  variant="hero"
                  className="w-full"
                  disabled={!isPaymentFormValid() || !areAllClientNamesFilled()}
                  onClick={handlePayment}
                >
                  Realizar Pagamento
                </Button>
                
                {(!areAllClientNamesFilled() || !isPaymentFormValid()) && (
                  <p className="text-xs text-muted-foreground text-center mt-2">
                    {!areAllClientNamesFilled()
                      ? "Preencha o nome do cliente em todos os servios"
                      : "Preencha todos os campos obrigatrios para continuar"}
                  </p>
                )}
              </CardContent>
            </Card>
          </div>
        </div>
      </main>

      <Footer />

      <Dialog open={pixDialogOpen} onOpenChange={setPixDialogOpen}>
        <DialogContent className="sm:max-w-md max-h-[90vh] overflow-y-auto">
          {pixState === PixPaymentState.PAYMENT && currentPix && (
            <PaymentScreen 
              data={currentPix} 
              onConfirm={handlePaymentConfirm}
              onBack={handleBackToForm}
            />
          )}

          {pixState === PixPaymentState.SUCCESS && currentPix && (
            <SuccessScreen 
              amount={currentPix.data.amount} 
              onReset={handlePixReset} 
            />
          )}
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default ConfirmBooking;

